@import "../../_globalColor";

.skill-main-div {
  display: flex;
  width: 100%;
  overflow: hidden;
}
.skills-text-div {
  margin-left: 50px;
}
.skill-main-div > * {
  flex: 1;
  // margin-bottom: 30px;
}
.skills-image-div > img {
  max-width: 100%;
  height: auto;
}
/* Media Query */
@media (max-width: 1380px) {
  .skills-heading {
    font-size: 40px;
  }
}
@media (max-width: 768px) {
  .skills-heading {
    font-size: 30px;
    text-align: center;
  }
  .greeting-text-p {
    font-size: 16px;
    line-height: normal;
  }
  .skill-main-div {
    flex-direction: column;
  }
  .skills-text-div {
    margin-left: 0px;
    margin: 20px;
  }
  .skills-text {
    font-size: 16px;
  }
  .skills-text-subtitle {
    font-size: 16px;
    text-align: center;
  }
  .skills-image-div {
    //  display: none; 
    order: 2;
  }
}

